    function handleStep(e){
        if(props.previewmode===false || (activeStepIndex > (e.target.getAttribute("stepIndex") || e.currentTarget.getAttribute("stepIndex")) || (window.isComponentValidated("wizardSubform",dataArray,true)===true) && props.previewmode===true) ){

            e.stopPropagation();            
            if(props.previewmode!== true){
            setQuickToolsClose(true, () => {

                setTimeout(() => {
      
                  setQuickToolsClose(false);
      
                }, 500);
      
              });  
            }
            if(style.navigationMethod==="viaBoth"){
        
        const stepId=e.target.getAttribute("name") || e.currentTarget.getAttribute("name");
        const stepIndex=e.target.getAttribute("stepIndex") || e.currentTarget.getAttribute("stepIndex");
        let subStepIndex = ""
        setActiveStep(stepId);        
        setActiveStepIndex(stepIndex);
        formState2.isStep={"parentStep":stepIndex};

        if(stepId!==activeStep){
            const firstSubStepIndex=props.data.children.filter((step) => step.id === stepId).shift()?.children.findIndex(function(substep){return substep?.elem_type?.toLowerCase()==="substep"});
            
            if(firstSubStepIndex!==-1){
                setActiveSubStep(props.data.children.filter((step) => step.id === stepId).shift()?.children[firstSubStepIndex].id);
                setActiveSubStepIndex(firstSubStepIndex?.toString());
                subStepIndex = firstSubStepIndex?.toString();
            } 
            
            else {
                setActiveSubStep("");
                setActiveSubStepIndex("");
                subStepIndex="";
            }   
        }
        if(containsSubsteps(props.data.children?.[stepIndex],"substep")) {
            setArrow("down");
        }
            setWizardIndexes({
                stepIndex: stepIndex,
                subStepIndex: subStepIndex ? subStepIndex : containsSubsteps(props.data.children?.[stepIndex],"substep") ? activeSubStepIndex : subStepIndex,
            })
            }
            if(window.isPortalLive)
                setMandatoryControls([])
            props.handleSelected(e,props.id,'n')
        }
    }
    
    function handleSubstep(e){     
        if(props.previewmode===false || ((activeSubStepIndex > e.target.getAttribute("subStepIndex") || window.isComponentValidated("wizardSubform",dataArray,true)===true) && props.previewmode===true) ){
            e.stopPropagation();
            if(props.previewmode!== true){

            setQuickToolsClose(true, () => {

                setTimeout(() => {
    
                setQuickToolsClose(false);
    
                }, 500);
    
            });  
            }
            if(style.navigationMethod==="viaBoth"){

            const stepId=e.target.getAttribute("name")||"";
            const subStepId=e.target.getAttribute("value");

            const stepIndex=e.target.getAttribute("stepIndex");
            const subStepIndex=e.target.getAttribute("subStepIndex");
            formState2.isStep={"parentStep":stepIndex, "parentSubStep":subStepIndex};

            if(stepId && stepId!==""){
                setActiveStep(stepId);
            }
            
            setActiveSubStep(subStepId);
            setActiveSubStepIndex(subStepIndex);
            setWizardIndexes({...wizardIndex,subStepIndex: subStepIndex})
            if(window.isPortalLive)
                setMandatoryControls([])
            props.handleSelected(e,props.id,'n')
            }
        }
    }

