    async function saveApplicationData(data_obj){
        setOpenLoader_fn(true);
        if(!formMataData)
            setIsLoading(true);
        var subformName = props?.data?.children[activeStepIndex]?.children[activeSubStepIndex]?.subformName ? props?.data?.children[activeStepIndex]?.children[activeSubStepIndex]?.subformName : props?.data?.children[activeStepIndex]?.subformName;        
        const applicationDataJson={
            "gdtoken":gdtoken,
            "witoken":witoken,
            "applicationId":openingAsApplication.applicationId,
            "applicationName": openingAsApplication.applicationName,
            "submitApplication":data_obj==="Y" || data_obj?.action==="close" ? "Y": "N",
            "stepindex": activeStepIndex || "",
            "stepsubindex": activeSubStepIndex || "",
            "formName": subformName,
        }
        const some_constant = await axios.post("/viewform/saveApplicationData",applicationDataJson,{withCredentials:true})
        .then((response)=>{
            if(response?.data?.error === "Y") {
                showAlertConfirmNotifications({
                    'open':true,
                    'showConfirm':'showNotification',
                    'alertType':'error',
                    'alertContentKey': `${t('There is some issue at server end. Kindly check with system administrator.')}`
                });
                setMoveToNextStep(false);
            }
            else {
                setMoveToNextStep(true);
            }
            if(response?.data?.APIData) {
                renderExecuteServerEventAPIData({outputArray: response?.data?.APIData, sync: true});
            }
            if(data_obj === "Y"){
                if(response?.headers?.workitemlist !== undefined && response?.headers?.workitemlist !== ""){
                    showAlertConfirmNotifications({
                        'open':true,
                        'showConfirm':'showNotification',
                        'alertType':'success',
                        vertical: 'bottom', 
                        horizontal: 'left', 
                        'alertContentKey':`${response.headers.workitemlist} ${t("saved successfully")}`,
                    });
                }
    
                    if(data_obj?.action==="close" || data_obj === "Y"){
                        setCloseApplication_wizard(true);
                    }

                window.setTimeout(() => {
                  setOpenLoader_fn(false);
                  showAlertConfirmNotifications({
                    'open':false,
                    'showConfirm':'showNotification',
                    'alertType':'success',
                    vertical: 'bottom', 
                    horizontal: 'left', 
                    'alertContentKey':`${response.headers.workitemlist} ${t("saved successfully")}`,
                });
                }, 500);
            }
            else if(data_obj?.action==="close"|| data_obj?.action1==="close"){
                logoutFunc();
            }
            else if(data_obj && data_obj === "N"){
                setIsLoading(false);
            }
            setOpenLoader_fn(false);
        })
        .catch(err =>{
            setOpenLoader_fn(false);
            setIsLoading(false);
        })        
    }

    async function increaseStep(){
        if(props.previewmode===false || (window.isComponentValidated("wizardSubform",dataArray,true) === true && props.previewmode===true) ){

            if(openingAsApplication?.applicationName){
                var submitApplication="N";
                if(activeStepIndex===children.length-1+""){
                    submitApplication="Y"
                }
                const dataObj={"submitApplication":submitApplication}
                await  saveApplicationData(dataObj);
            }

            if(stepsArray.length-1 > parseInt(activeStepIndex) && moveToNextStep.current){
                setIsLoading(true);
                setLoadingMessage("Saving data...");
                setActiveStepIndex(prev =>{
                    return parseInt(prev)+1+""
                });

                setActiveStep(children[parseInt(activeStepIndex)+1].id);

                document.querySelector(`.y_eachStep${children[parseInt(activeStepIndex)+1]?.id}`)?.scrollIntoView()

                if(containsSubsteps(children[parseInt(activeStepIndex)+1] , "substep")===true){
                    setActiveSubStepIndex(prev =>{
                        return "0"
                    });
                    setActiveSubStep(children[parseInt(activeStepIndex)+1].children[0].id);
                    setWizardIndexes({
                        stepIndex: parseInt(activeStepIndex)+1+"",
                        subStepIndex: "0",
                    })
                    setArrow("down");
                }
                else{
                    setActiveSubStepIndex(prev =>{
                        return "" 
                    });

                    setActiveSubStep("");
                    setWizardIndexes({
                        stepIndex: parseInt(activeStepIndex)+1+"",
                        subStepIndex: "",
                    })
                }
            }
        }
    }  

      async function decreaseStep(){

            if(openingAsApplication?.applicationName){
                await saveApplicationData();
            }

            if(0 < parseInt(activeStepIndex) && moveToNextStep.current){
                setIsLoading(true);
                setLoadingMessage("Saving data...");
                setActiveStepIndex(prev =>{
                return parseInt(prev)-1+""
                });
                setActiveStep(children[parseInt(activeStepIndex)-1].id);

                document.querySelector(`.y_eachStep${children[parseInt(activeStepIndex)-1]?.id}`)?.scrollIntoView()
                if(containsSubsteps( children[parseInt(activeStepIndex)-1] ,"substep")===true){

                    setActiveSubStepIndex(prev =>{
                        return children[parseInt(activeStepIndex)-1].children.length-1+""
                    });

                    setActiveSubStep(children[parseInt(activeStepIndex)-1].children[children[parseInt(activeStepIndex)-1].children.length-1].id);
                    setWizardIndexes({
                        stepIndex: parseInt(activeStepIndex)-1+'',
                        subStepIndex: children[parseInt(activeStepIndex)-1].children.length-1+"",
                    })
                    setArrow("down");
                }
                else{
                    setActiveSubStepIndex(prev =>{
                        return "" 
                    });

                    setActiveSubStep("");
                    setWizardIndexes({
                        stepIndex: parseInt(activeStepIndex)-1+'',
                        subStepIndex: "",
                    })
                }
            }
            if(window.isPortalLive)
                setMandatoryControls([])
        }

      async function increaseSubStep(){
        if(props.previewmode===false || (window.isComponentValidated("wizardSubform",dataArray,true)===true && props.previewmode===true) ){
            if(openingAsApplication?.applicationName){
            var submitApplication="N";
            if(activeStepIndex===children.length-1+""){
                submitApplication="Y"
            }
            const dataObj={"submitApplication":submitApplication}
            await saveApplicationData(dataObj);
            }

            if(moveToNextStep.current) {
                setIsLoading(true);
                setLoadingMessage("Saving data...");
                if(children[parseInt(activeStepIndex)].children.length === parseInt(activeSubStepIndex) +1 ){
                increaseStep();
                }
                else if(children[parseInt(activeStepIndex)].children.length > parseInt(activeSubStepIndex)+1 ){
    
                    setActiveSubStepIndex(prev =>{
                        return parseInt(activeSubStepIndex)+1+""
                    });
                    setActiveSubStep(children[parseInt(activeStepIndex)].children[parseInt(activeSubStepIndex)+1].id);
                    setWizardIndexes({...wizardIndex,subStepIndex: parseInt(activeSubStepIndex)+1+""})
                }
            }
        }
      }

      async function decreaseSubStep(){

            if(openingAsApplication?.applicationName){
                await saveApplicationData();
            }
            if(parseInt(activeSubStepIndex)===0 && moveToNextStep.current){
                setIsLoading(true);
                setLoadingMessage("Saving data...");
                decreaseStep();
            }
            else if(0 < parseInt(activeSubStepIndex) && moveToNextStep.current){
                setIsLoading(true);
                setLoadingMessage("Saving data...");
                setActiveSubStepIndex(prev =>{
                    return parseInt(activeSubStepIndex)-1+""
                });

                setActiveSubStep(children[parseInt(activeStepIndex)].children[parseInt(activeSubStepIndex)-1].id);
                setWizardIndexes({...wizardIndex,subStepIndex: parseInt(activeSubStepIndex)-1+""})
            }
            if(window.isPortalLive)
                setMandatoryControls([])

        }


